import { Injectable } from '@angular/core';
import { cloneDeep, isString } from 'lodash';
import { AdaptRxDatetimeAdapter, RxDatetimeStruct } from '@bmc-ux/adapt-angular';
import moment from 'moment-es6';

@Injectable()
export class TimeAdapter extends AdaptRxDatetimeAdapter<string> {
  private storeTimeFormat = 'HH:mm:ss';

  fromModel(dateString: string): RxDatetimeStruct {
    let adaptedValue: RxDatetimeStruct = null;
    const date = moment(dateString, 'LTS');

    if (date && date.isValid()) {
      adaptedValue = {
        year: date.year(),
        month: date.month(),
        date: date.date(),
        hours: date.hours(),
        minutes: date.minutes(),
        seconds: date.seconds()
      };
    }

    return adaptedValue;
  }

  toModel(date: RxDatetimeStruct): string {
    return date
      ? date.parseError
        ? moment.invalid().toString()
        : moment({
            hour: date.hours || 0,
            minute: date.minutes || 0,
            seconds: date.seconds || 0
          }).format(this.storeTimeFormat)
      : null;
  }
}
